<template lang="pug">
#canvas-container
  .title-container
    p Hey there! I'm
    //- p I'm #[span.typing-demo(:style="{ width: currMessage.length + 'ch', 'animation-timing-function': 'steps(' + currMessage.length + ')' }") {{ currMessage }}]
    p #[span.typewriter-span.highlight-text {{ typeWriteDisplay }}]
    .cta-container(@click="hideParticle")
      p.sub-text About Me
      .arrow
        svg.bi.bi-arrow-right-short(
          width="32px",
          height="32px",
          viewBox="0 0 16 16",
          fill="currentColor",
          xmlns="http://www.w3.org/2000/svg"
        )
          path(
            fill-rule="evenodd",
            d="M4 8a.5.5 0 0 1 .5-.5h5.793L8.146 5.354a.5.5 0 1 1 .708-.708l3 3a.5.5 0 0 1 0 .708l-3 3a.5.5 0 0 1-.708-.708L10.293 8.5H4.5A.5.5 0 0 1 4 8z"
          )

        //- svg.bi.bi-arrow-right(
        //-   width="1em",
        //-   height="1em",
        //-   viewBox="0 0 16 16",
        //-   fill="currentColor",
        //-   xmlns="http://www.w3.org/2000/svg"
        //- )
        //-   path(
        //-     fill-rule="evenodd",
        //-     d="M1 8a.5.5 0 0 1 .5-.5h11.793l-3.147-3.146a.5.5 0 0 1 .708-.708l4 4a.5.5 0 0 1 0 .708l-4 4a.5.5 0 0 1-.708-.708L13.293 8.5H1.5A.5.5 0 0 1 1 8z"
        //-   )

  vue-particles(
    color="#dedede",
    :particleOpacity="0.7",
    :particlesNumber="80",
    shapeType="circle",
    :particleSize="4",
    linesColor="#dedede",
    :linesWidth="1",
    :lineLinked="true",
    :lineOpacity="0.4",
    :linesDistance="150",
    :moveSpeed="3",
    :hoverEffect="true",
    hoverMode="grab",
    :clickEffect="true",
    clickMode="repulse"
  )
</template>

<script>
export default {
  data() {
    return {
      messages: ["Tan Wei Shen", "a Software Engineer", "a Problem Solver"],
      typeWriteDisplay: "",
      typeWriteIndex: 0,
      messageIndex: 0,
      typeInterval: {},
      typeTimeout: {},
      typeIndexInterval: {},
    };
  },
  computed: {
    currMessage() {
      return this.messages[this.messageIndex];
    },
  },
  methods: {
    animateWrite() {
      const limit = 2000;
      const intervalFrame = limit / this.currMessage.length;
      this.typeIndexInterval = setInterval(() => {
        this.typeWriteDisplay += this.currMessage[this.typeWriteIndex];
        this.typeWriteIndex += 1;
        if (this.typeWriteIndex >= this.currMessage.length) {
          this.typeWriteIndex = 0;
          clearInterval(this.typeIndexInterval);
        }
      }, intervalFrame);
    },
    animateDelete() {
      const limit = 1000;
      const intervalFrame = limit / this.currMessage.length;
      this.typeIndexInterval = setInterval(() => {
        this.typeWriteDisplay = this.typeWriteDisplay.slice(
          0,
          this.typeWriteDisplay.length - 1
        );
        if (this.typeWriteDisplay.length <= 0) {
          this.typeWriteIndex = 0;
          clearInterval(this.typeIndexInterval);
        }
      }, intervalFrame);
    },
    hideParticle() {
      const canvas = document.getElementById("canvas-container");
      canvas.style.transform = "translateY(-100vh)";
      setTimeout(() => {
        this.$store.commit("setShowContent", true);
      }, 100);
      clearInterval(this.typeInterval);
      clearInterval(this.typeIndexInterval);
      clearTimeout(this.typeTimeout);
    },
    initTypewriter() {
      this.animateWrite();
      this.typeInterval = setInterval(() => {
        this.animateDelete();
        this.typeTimeout = setTimeout(() => {
          this.messageIndex++;
          if (this.messageIndex % this.messages.length === 0) {
            this.messageIndex = 0;
          }
          this.animateWrite();
        }, 1500);
      }, 4000);
    },
  },
  mounted() {
    this.initTypewriter();
    window.onblur = () => {
      clearInterval(this.typeInterval);
      clearInterval(this.typeIndexInterval);
      clearTimeout(this.typeTimeout);
      this.typeWriteDisplay = "";
      this.typeWriteIndex = 0;
    };
    window.onfocus = () => {
      this.initTypewriter();
    };
  },
};
</script>

<style lang="sass" scoped>
@import '../assets/sass/variables.sass'

#canvas-container
  height: 100vh
  width: 100%
  // background-image: linear-gradient(180deg, #7EC1CC, #3DAEC2, #2B7B8A)
  // background-image: linear-gradient(180deg, #7EC1CC, #2B7B8A)
  background-color: #2B7B8A
  position: absolute
  transition: all 1s ease
  z-index: 1500
  transform: translateY(0)

#particles-js
  width: 100%
  height: 100%

.title-container
  position: absolute
  top: 50%
  left: 50%
  user-select: none
  transform: translate(-50%, -50%)
  font-size: 42px
  text-align: center
  background-color: rgba(43, 123, 138, 0.75)
  // background-color: rgba(0, 0, 0, 0.25)
  // border-radius: 0px
  padding: 40px
  border-radius: 40px
  // min-width: 600px
  +mobile
    font-size: 30px
    width: 100%
    box-sizing: border-box

.highlight-text
  color: #87ceeb
  // color: #23A0D3
  font-weight: bold

.sub-text
  font-size: 18px

.cta-container
  display: flex
  justify-content: center
  align-items: center
  height: 40px
  margin: 20px auto 0px
  border: 1px solid #fff
  width: 170px
  cursor: pointer
  border-radius: 4px
  transition: all 0.25s ease
  .arrow
    transition: transform 0.25s ease

.cta-container:hover
  background-color: #c7e4e9
  color: #2B7B8A
  .sub-text
    color: #2B7B8A
  .arrow
    transform: rotate(-90deg)

.arrow
  display: flex
  margin-left: 5px
  width: 32px
  height: 32px
  align-items: center
  justify-content: center
  cursor: pointer

.typing-demo
  width: 22ch
  animation: typing 1s
  animation-timing-function: steps(22)
  white-space: nowrap
  overflow: hidden

.typewriter-span
  border-right: 3px solid #30DEFF
</style>
